:root {
	--primary: #0169ff;
	--default-light: #fff;
	--default-dark: #333;
	--button-color: var(--primary);
	--btn-default-bg: transparent;
	--btn-default-color: var(--default-light);
	--btn-primary-bg: var(--primary);
	--btn-primary-color: var(--default-light);
	--bodyfont: "Inter var", sans-serif;
	--headfont: "Inter var", sans-serif;
	--body-font-family: var(--bodyfont);
	--head-font-family: var(--headfont);
}
// @import url("https://rsms.me/inter/inter.css");
// @import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
.pqc {
	// * Override uikit varibles
	@import "helpers/_overwrites";
	body {
		font-family: $base-body-font-family;
		font-size: $base-body-font-size;
		font-weight: $base-body-font-weight;
		line-height: $base-body-line-height;
		/* 2 */
		-webkit-text-size-adjust: 100%;
		/* 3 */
		background: $base-body-background;
		color: $base-body-color;
		@if (mixin-exists(hook-base-body)) {
			@include hook-base-body();
		}
	}
	font-family: $base-body-font-family;
	font-size: $base-body-font-size;
	font-weight: $base-body-font-weight;
	line-height: $base-body-line-height;
	/* 2 */
	-webkit-text-size-adjust: 100%;
	/* 3 */
	background: $base-body-background;
	color: $base-body-color;
	@if (mixin-exists(hook-base-body)) {
		@include hook-base-body();
	}

	// 2. Import default variables and available mixins.
	@import "./node_modules/uikit/src/scss/variables-theme.scss";
	@import "./node_modules/uikit/src/scss/mixins-theme.scss";

	// 3. Your custom mixin overwrites.
	// @mixin hook-card() { color: #000; }

	// 4. Import UIkit.
	@import "./node_modules/uikit/src/scss/uikit-theme.scss";

	@import "base";
	// @import './default/buttons';
}
