/*
* Set the fill and stroke color of all SVG elements to the current text color
*/
/*! purgecss start ignore */
.uk-icon:not(.uk-preserve) [fill*="#"]:not(.uk-preserve) {
	fill: currentcolor;
}
.uk-icon:not(.uk-preserve) [stroke*="#"]:not(.uk-preserve) {
	stroke: currentcolor;
}

.uk-border {
	border: 1px solid $global-border;
}

.text-primary {
	color: $global-primary-background;
}

.border-primary {
	border-color: $global-primary-background;
}

.uk-text-medium {
	font-weight: 500 !important;
}

b,
strong {
	@apply font-bold;
}

.uk-dark {
	.uk-light {
		h1,
		h2,
		h3,
		h4,
		h5,
		h6,
		.desc {
			color: $inverse-base-heading-color !important;
		}
		.uk-text-primary {
			color: $text-primary-color;
		}
		.uk-button-link {
			color: var(--default-light) !important;
			opacity: 0.8;
			&:hover {
				opacity: 1;
			}
		}
		.uk-text-meta {
			color: var(--default-light);
		}
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6,
	.desc {
		color: $global-emphasis-color !important;
	}
	.desc {
		color: $global-color !important;
	}

	.uk-text-primary {
		color: var(--primary);
		// font-weight: 600;
	}

	.uk-badge {
		background-color: $badge-background;
		padding: 10px 10px;
		color: $badge-color;
		@if (mixin-exists(hook-badge)) {
			@include hook-badge();
		}
	}

	.uk-list-bullet > ::before {
		@include svg-fill(
			$internal-list-bullet-image,
			"#000",
			$list-bullet-icon-color
		);
	}

	.uk-badge:hover,
	.uk-badge:focus {
		color: $badge-hover-color;
		@if (mixin-exists(hook-badge-hover)) {
			@include hook-badge-hover();
		}
	}

	.uk-button-primary {
		background-color: var(--btn-primary-bg);
		color: var(--btn-primary-color);
		@if (mixin-exists(hook-button-primary)) {
			@include hook-button-primary();
		}
	}

	.uk-button-primary:hover,
	.uk-button-primary:focus {
		// background-color: $button-primary-hover-background;
		// color: $button-primary-hover-color;
		// box-shadow: 0 5px 15px rgb(0 0 0 / 8%);
		background-color: var(--btn-primary-bg);
		color: var(--btn-primary-color);
		box-shadow: 0 14px 25px rgb(0 0 0 / 16%);
		@if (mixin-exists(hook-button-primary-hover)) {
			@include hook-button-primary-hover();
		}
	}

	.uk-button-primary:active,
	.uk-button-primary.uk-active {
		background-color: $button-primary-active-background;
		color: $button-primary-active-color;
		@if (mixin-exists(hook-button-primary-active)) {
			@include hook-button-primary-active();
		}
	}

	.uk-button-default:hover,
	.uk-button-default:focus {
		box-shadow: 0 14px 25px rgb(0 0 0 / 16%);
	}

	.uk-button-link {
		color: var(--btn-primary-bg);
	}
}

.uk-light {
	.uk-dark {
		h1,
		h2,
		h3,
		h4,
		h5,
		h6,
		.desc {
			color: $global-emphasis-color !important;
		}
		.uk-button-default {
			border: 1px solid var(--btn-primary-bg) !important;
			color: var(--btn-primary-bg) !important;
			@if (mixin-exists(hook-button-primary)) {
				@include hook-button-primary();
			}
		}
		.uk-text-meta {
			color: var(--default-dark);
		}
	}

	.text-primary {
		color: $inverse-base-heading-color;
	}

	.uk-text-meta {
		color: var(--default-light);
	}
	.border-primary {
		border-color: $inverse-base-heading-color;
	}

	.uk-text-medium {
		font-weight: 500 !important;
	}
	.uk-button-default {
		border: 1px solid var(--btn-default-color) !important;
		color: var(--btn-default-color) !important;
		@if (mixin-exists(hook-button-primary)) {
			@include hook-button-primary();
		}
	}
}

// Responsive styles for Headings

/* Phone */
@media only screen and (max-width: $breakpoint-small) {
	h1,
	.uk-h1 {
		font-size: 2rem;
	}

	h2,
	.uk-h2 {
		font-size: 1.6rem;
	}

	h3,
	.uk-h3 {
		font-size: 1.2rem;
	}

	h4,
	.uk-h4 {
		font-size: 1rem;
	}

	h5,
	.uk-h5 {
		font-size: 1rem;
	}

	h6,
	.uk-h6 {
		font-size: 1rem;
	}

	body,
	.desc {
		font-size: 14px;
	}

	.uk-button {
		font-size: 14px;
		line-height: 36px;
		// padding: 0 20px;
		// &.uk-margin-right {
		// 	margin-right: 0px !important;
		// }
	}
}

/* Phone landscape and bigger */
@media (min-width: $breakpoint-small) {
	h1,
	.uk-h1 {
		font-size: 2.2rem;
	}

	h2,
	.uk-h2 {
		font-size: 1.7rem;
	}

	h3,
	.uk-h3 {
		font-size: 1.4rem;
	}

	h4,
	.uk-h4 {
		font-size: 1.2rem;
	}

	h5,
	.uk-h5 {
		font-size: 1rem;
	}

	h6,
	.uk-h6 {
		font-size: 1rem;
	}

	body,
	.desc {
		font-size: 14px;
	}

	.uk-button {
		font-size: 14px;
		line-height: 36px;
		// padding: 0 20px;
	}
}

/* Tablet landscape and bigger */
@media (min-width: $breakpoint-medium) {
	h1,
	.uk-h1 {
		font-size: 2.5rem;
	}

	h2,
	.uk-h2 {
		font-size: 1.8rem;
	}

	h3,
	.uk-h3 {
		font-size: 1.4rem;
	}

	h4,
	.uk-h4 {
		font-size: 1.3rem;
	}

	h5,
	.uk-h5 {
		font-size: 1rem;
	}

	h6,
	.uk-h6 {
		font-size: 1rem;
	}

	body,
	.desc {
		font-size: 15px;
	}

	.uk-button {
		font-size: 15px;
		line-height: 38px;
		// padding: 0 20px;
	}
}

/* Desktop and bigger */
@media (min-width: $breakpoint-large) {
	h1,
	.uk-h1 {
		font-size: 2.6rem;
	}

	h2,
	.uk-h2 {
		font-size: 2rem;
	}

	h3,
	.uk-h3 {
		font-size: 1.5rem;
	}

	h4,
	.uk-h4 {
		font-size: 1.2rem;
	}

	h5,
	.uk-h5 {
		font-size: 1.1rem;
	}

	h6,
	.uk-h6 {
		font-size: 1rem;
	}

	body,
	.desc {
		font-size: 16px;
	}

	.uk-button {
		font-size: 16px;
		line-height: 38px;
		// padding: 0 20px;
	}
}

h1 i,
h2 i,
h3 i,
h4 i {
	font-style: normal;
	font-weight: 300;
}

